import React, { useState } from 'react';
import { View, TextInput, Button, Text, StyleSheet } from 'react-native';
import useStore from '../store/store'; //  zustand store

const Login = ({ navigation }) => {  // navigation props
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const { user, setUsername: setUsernameInStore, setUsernameAsync } = useStore();

  const handleLogin = async () => {
    if (username && password) {
      try {
        await setUsernameAsync(username);  // 
        alert('');
        navigation.replace('CreateChildren');
      } catch (error) {
        alert('');
      }
    } else {
      alert('');
    }
  };

  const handleRegister = () => {
    if (username && password) {
      alert('');
    } else {
      alert('');
    }
  };

  return (
      <View style={styles.container}>
        <Text style={styles.title}></Text>
        <TextInput
            style={styles.input}
            placeholder=""
            value={username}
            onChangeText={setUsername}
        />
        <TextInput
            style={styles.input}
            placeholder=""
            value={password}
            onChangeText={setPassword}
            secureTextEntry
        />
        <Button title="" onPress={handleLogin} />
        <Text style={styles.orText}></Text>
        <Button title="" onPress={handleRegister} />
        {user?.username ? <Text>, {user.username}!</Text> : null}
      </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: 16,
    backgroundColor: '#E0F7FA', // 
  },
  title: {
    fontSize: 24,
    marginBottom: 20,
  },
  input: {
    width: '33%',
    padding: 12,
    marginBottom: 10,
    borderWidth: 1,
    borderColor: '#ddd',
    borderRadius: 4,
  },
  orText: {
    marginVertical: 10,
    fontSize: 16,
  },
});

export default Login;
