import React, { useState } from 'react';
import { View, TextInput, Button, StyleSheet, Image, Alert, ScrollView } from 'react-native';
import axios from 'axios';

const ImageGenerator = () => {
    const [description, setDescription] = useState(''); // 
    const [imageUrl, setImageUrl] = useState(''); //  URL
    const [loading, setLoading] = useState(false); // 

    const handleGenerateImage = async () => {
        if (!description.trim()) {
            Alert.alert('', '');
            return;
        }

        setLoading(true);
        try {
            const response = await axios.post(
                'http://47.242.78.104:6088/i/pic', // 
                {
                    uid: 'a81s', // 
                    picreq: description, // 
                },
                {
                    headers: {
                        'Content-Type': 'application/json', // 
                    },
                }
            );

            const { data } = response.data; //  URL
            setImageUrl(data); //  URL
        } catch (error) {
            console.error(':', error);
            Alert.alert('', '');
        } finally {
            setLoading(false);
        }
    };

    return (
        <ScrollView contentContainerStyle={styles.container}>
            <TextInput
                style={styles.input}
                placeholder=""
                value={description}
                onChangeText={setDescription}
                editable={!loading} // 
            />
            <Button title={loading ? '...' : ''} onPress={handleGenerateImage} disabled={loading} />
            {imageUrl ? (
                <Image
                    source={{ uri: imageUrl }}
                    style={styles.image}
                    resizeMode="contain"
                />
            ) : null}
        </ScrollView>
    );
};

const styles = StyleSheet.create({
    container: {
        flexGrow: 1,
        justifyContent: 'center',
        alignItems: 'center',
        padding: 16,
        backgroundColor: '#f5f5f5',
    },
    input: {
        width: '100%',
        padding: 12,
        marginBottom: 20,
        borderWidth: 1,
        borderColor: '#ccc',
        borderRadius: 8,
        backgroundColor: '#fff',
    },
    image: {
        width: 300,
        height: 300,
        marginTop: 20,
        borderWidth: 1,
        borderColor: '#ccc',
    },
});

export default ImageGenerator;
