import React, { useEffect, useState } from 'react';
import {
    View,
    Text,
    StyleSheet,
    ScrollView,
    TouchableOpacity,
    ActivityIndicator,
    Image,
    Pressable,
} from 'react-native';
import useStore from '../store/store.jsx'; //  zustand store
import { gptQuery, generateImage } from '../utils/api'; //  GPT  API

const DisplayStoreData = () => {
    const currentChildren = useStore((state) => state.currentChildren); //  store  currentChildren
    const learningGoals = useStore((state) => state.learningGoals);     //  store  learningGoals

    const [generatedData, setGeneratedData] = useState(null);     // 
    const [sceneImage, setSceneImage] = useState(null);           // 
    const [elementImages, setElementImages] = useState([]);       // 
    const [loading, setLoading] = useState(false);                // 
    const [imageLoading, setImageLoading] = useState(false);      // 

    // 
    const [selectedElements, setSelectedElements] = useState([]);

    // 
    const toggleElementSelection = (index) => {
        setSelectedElements((prevSelected) => {
            if (prevSelected.includes(index)) {
                // 
                return prevSelected.filter((i) => i !== index);
            } else {
                // 
                return [...prevSelected, index];
            }
        });
    };

    //  GPT 
    const generateData = async () => {
        setLoading(true);
        try {
            const prompt = `{}...50. .1. 5 Current Children 2. LearningGoalsA,B,C3Current Children: ${JSON.stringify(currentChildren).replace(/"/g, "'")}, Learning Goals: ${JSON.stringify(learningGoals).replace(/"/g, "'")}`;
            const gptResponse = await gptQuery(prompt); //  GPT API
            let results;
            try {
                results = JSON.parse(gptResponse);
            } catch (parseError) {
                console.error('Error parsing GPT response:', parseError);
                throw new Error('Invalid JSON format in GPT response.');
            }
            console.log('Generated Data from GPT:', results);
            setGeneratedData(results);
        } catch (error) {
            console.error('Error generating data from GPT:', error);
        } finally {
            setLoading(false);
        }
    };

    // 
    const generateImages = async () => {
        setImageLoading(true);
        try {
            //  Prompt 
            const scenePrompt = `,2${JSON.stringify(
                learningGoals
            )}`;

            //  GPT  5  generatedData.words 
            const elementPrompts =
                generatedData?.words?.map(
                    (word) => `${word}`
                ) || [];

            const sceneResponse = await generateImage(scenePrompt); //  API 
            const elementResponses = await Promise.all(
                elementPrompts.map((prompt) => generateImage(prompt))
            ); //  API 
            setSceneImage(sceneResponse); //  URL
            setElementImages(elementResponses); //  URL 
            setSelectedElements([]); // 
        } catch (error) {
            console.error('Error generating images:', error);
        } finally {
            setImageLoading(false);
        }
    };

    useEffect(() => {
        //  currentChildren  learningGoals 
        generateData();
    }, [currentChildren, learningGoals]);

    return (
        <View style={styles.container}>
            <ScrollView contentContainerStyle={styles.scrollContainer}>
                {/* ========  ======== */}
                {generatedData && (
                    <View style={styles.section}>
                        <Text style={styles.title}></Text>
                        <Text style={styles.content}>
                            {generatedData.words?.join(', ') || ''}
                        </Text>
                    </View>
                )}

                {generatedData?.[''] &&
                    Object.entries(generatedData['']).map(([key, steps]) => (
                        <View key={key} style={styles.section}>
                            <Text style={styles.title}>{key} </Text>
                            {Object.entries(steps).map(([stepKey, stepValue]) => (
                                <View key={stepKey} style={styles.stepContainer}>
                                    <Text style={styles.stepKey}>{stepKey}:</Text>
                                    <Text style={styles.stepValue}>{stepValue}</Text>
                                </View>
                            ))}
                        </View>
                    ))}

                {/* ========  ======== */}
                {imageLoading ? (
                    <ActivityIndicator size="large" color="#007BFF" />
                ) : (
                    <>
                        {/*  */}
                        <View style={styles.imageContainer}>
                            {sceneImage && (
                                <Image
                                    source={{ uri: sceneImage }}
                                    style={styles.sceneImage}
                                    resizeMode="contain" // 
                                />
                            )}
                        </View>
                        {/* / */}
                        {elementImages.length > 0 && (
                            <View style={styles.elementsWrapper}>
                                {elementImages.map((imgUri, index) => {
                                    const isSelected = selectedElements.includes(index);
                                    return (
                                        <Pressable
                                            key={index}
                                            onPress={() => toggleElementSelection(index)}
                                            style={[
                                                styles.elementImageWrapper,
                                                isSelected && styles.selectedBorder,
                                            ]}
                                        >
                                            <Image
                                                source={{ uri: imgUri }}
                                                style={styles.elementImage}
                                            />
                                        </Pressable>
                                    );
                                })}
                            </View>
                        )}
                    </>
                )}
            </ScrollView>

            {/* ========  ======== */}
            <TouchableOpacity
                style={[styles.regenerateButton, loading && styles.disabledButton]}
                onPress={generateData}
                disabled={loading}
            >
                <Text style={styles.buttonText}>
                    {loading ? '...' : ''}
                </Text>
            </TouchableOpacity>

            <TouchableOpacity
                style={[styles.generateImagesButton, imageLoading && styles.disabledButton]}
                onPress={generateImages}
                disabled={imageLoading}
            >
                <Text style={styles.buttonText}>
                    {imageLoading ? '...' : ''}
                </Text>
            </TouchableOpacity>
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        padding: 16,
        backgroundColor: '#f5f5f5',
    },
    scrollContainer: {
        alignItems: 'flex-start',
        paddingVertical: 16,
    },
    section: {
        marginBottom: 20,
        padding: 16,
        borderWidth: 1,
        borderColor: '#ccc',
        borderRadius: 8,
        backgroundColor: '#fff',
        width: '100%',
    },
    title: {
        fontSize: 18,
        fontWeight: 'bold',
        marginBottom: 8,
    },
    content: {
        fontSize: 14,
        color: '#333',
        marginBottom: 4,
    },
    stepContainer: {
        marginBottom: 8,
        paddingLeft: 10,
    },
    stepKey: {
        fontSize: 16,
        fontWeight: '600',
        color: '#555',
    },
    stepValue: {
        fontSize: 14,
        color: '#333',
    },
    imageContainer: {
        width: '100%',
        height: 300,
        marginTop: 20,
    },
    sceneImage: {
        width: '100%',
        height: '100%',
        borderRadius: 8,
        marginBottom: 16,
    },
    elementsWrapper: {
        flexDirection: 'row',
        flexWrap: 'wrap',
        marginTop: 16,
        justifyContent: 'center', // 
        alignItems: 'center', // 
    },
    elementImageWrapper: {
        margin: 5,
        borderRadius: 8,
        borderWidth: 2,
        borderColor: 'transparent',
        overflow: 'hidden',
        width: 80, // 
        height: 80, // 
    },
    elementImage: {
        width: '100%',
        height: '100%',
        borderRadius: 8,
    },
    selectedBorder: {
        borderColor: '#007BFF',
    },
    regenerateButton: {
        marginTop: 16,
        paddingVertical: 12,
        paddingHorizontal: 20,
        backgroundColor: '#007BFF',
        borderRadius: 8,
        alignItems: 'center',
    },
    generateImagesButton: {
        marginTop: 16,
        paddingVertical: 12,
        paddingHorizontal: 20,
        backgroundColor: '#28A745',
        borderRadius: 8,
        alignItems: 'center',
    },
    disabledButton: {
        backgroundColor: '#ccc',
    },
    buttonText: {
        color: '#fff',
        fontSize: 16,
        fontWeight: 'bold',
    },
});

export default DisplayStoreData;
